import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';
import { CustomFieldsComponent } from './custom-fields.component';
import { CustomFieldsListComponent } from './custom-fields-list.component';
import { LayoutModule } from '../../../layouts/layout.module';
import { DefaultComponent } from '../../default.component';
import {HttpClientModule} from '@angular/common/http';
import { HTTP_INTERCEPTORS } from '@angular/common/http';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {BrowserAnimationsModule} from '@angular/platform-browser/animations';
import {CdkTableModule} from '@angular/cdk/table';
import {FormControl, Validators, FormGroup} from '@angular/forms'; 
import {ToastModule} from '../../../layouts/toast-notification/toast.module';
import {ListModule} from '../../list/list.module';
import {
	  MatAutocompleteModule,
	  MatButtonModule, 
	  MatButtonToggleModule,
	  MatCardModule,
	  MatCheckboxModule,
	  MatChipsModule,
	  MatDatepickerModule,
	  MatDialogModule,
	  MatExpansionModule,
	  MatGridListModule,
	  MatIconModule,
	  MatInputModule,
	  MatListModule,
	  MatMenuModule,
	  MatNativeDateModule,
	  MatPaginatorModule,
	  MatProgressBarModule,
	  MatProgressSpinnerModule,
	  MatRadioModule,
	  MatRippleModule,
	  MatSelectModule,
	  MatSidenavModule,
	  MatSliderModule,
	  MatSlideToggleModule,
	  MatSnackBarModule,
	  MatSortModule,
	  MatTableModule,
	  MatTabsModule,
	  MatToolbarModule,
	  MatTooltipModule,
	  MatStepperModule,
	} from '@angular/material';

const routes: Routes = [
    {
        "path": "",
        "component": DefaultComponent,
        "children": [
            {
                "path": "",
                "component": 
				CustomFieldsListComponent
            }
        ]
    },
	{
        "path": "custom-fields",
        "component": DefaultComponent,
        "children": [
            {
                "path": "",
                "component": 
				CustomFieldsComponent
            }
        ]
    }
];

@NgModule({
    imports: [
        CommonModule, RouterModule.forChild(routes), LayoutModule, HttpClientModule,
        FormsModule, ReactiveFormsModule, ToastModule,ListModule,
		MatInputModule, MatNativeDateModule, MatCardModule, MatSelectModule, MatSidenavModule, MatTableModule, MatSortModule, MatPaginatorModule,MatDatepickerModule, MatCheckboxModule, MatRadioModule
    ], 
	exports: [ RouterModule ], 
	declarations: [ CustomFieldsComponent, CustomFieldsListComponent ],
})

export class CustomFieldsModule  {

}


