import { Component, OnInit, ViewEncapsulation, AfterViewInit, ElementRef, ViewChild, HostBinding} from '@angular/core';
import { Helpers } from '../../../../helpers';
import { ScriptLoaderService } from '../../../../_services/script-loader.service';
import { FLY_IN_OUT_ANIMATION }   from '../../../../animations/fly-in-out.animation';
import { ROUND_ANTICLOCK_ANIMATION }   from '../../../../animations/round-anticlock.animation';
import {HttpClientModule} from '@angular/common/http';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import { Router, Event, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';
import { ToastComponent } from '../../../layouts/toast-notification/toast.component';
import { FormControl, Validators, FormGroup, ValidationErrors } from '@angular/forms';
const NUM_REGEX=/^\d$/;

@Component({
    selector: ".m-grid__item.m-grid__item--fluid.m-wrapper",
    templateUrl: "./custom-fields.component.html",
    encapsulation: ViewEncapsulation.None,
	animations: [ FLY_IN_OUT_ANIMATION,ROUND_ANTICLOCK_ANIMATION ]
})

export class CustomFieldsComponent implements OnInit {
	@HostBinding('@flyInOutTrigger') flyInOutTrigger = 'in';
	@ViewChild(ToastComponent) toast: ToastComponent;
	 
	public customfieldId = '0'; 
	public status;	
	public customfieldForm: FormGroup;
	public customfield = new customfieldData(); 
	public Module = [];
	public subModule = [];
	public msg = [];
	 
	constructor( private http: HttpClient, private _router: Router, private route:ActivatedRoute) { 
	
	 this.route.queryParams.filter(params => params.customfieldId).subscribe(params=>{
			if(params.customfieldId != undefined){
				this.customfieldId=params.customfieldId; 
			}
		}); 
	
		if(this.customfieldId == '0'){
		this.status = "Add";
//			this.populateDropDownData();
		}else{
			this.status = "Update";
//			this.populateData();
		}
	}
	 
	ngOnInit() 
	{
		this.customfieldForm = new FormGroup({			
//			'' : new FormControl('', [ Validators.required]),
			'customfieldSubmoduleId' : new FormControl(''),
			'customfieldTitle' : new FormControl(''),
			'customfieldCftypeId' : new FormControl(''),
			'customfieldOption' : new FormControl(''),
			'customfieldNumeric' : new FormControl(''),
			'customfieldLengthMin' : new FormControl('', [ Validators.pattern(NUM_REGEX)]),
			'customfieldLengthMax' : new FormControl('', [ Validators.pattern(NUM_REGEX)]),
			'customfieldUnique' : new FormControl(''),
			'customfieldMandatory' : new FormControl(''),
			'customfieldInstruction' : new FormControl(''),
			'customfieldPrint' : new FormControl(''),
			'customfieldActive' : new FormControl(''),
		});
	}
	 
	 get customfieldSubmoduleId(){ return this.customfieldForm.get('customfieldSubmoduleId')};
	 get customfieldTitle(){ return this.customfieldForm.get('customfieldTitle')};
	 get customfieldCftypeId(){ return this.customfieldForm.get('customfieldCftypeId')};
	 get customfieldOption(){ return this.customfieldForm.get('customfieldOption')};
	 get customfieldNumeric(){ return this.customfieldForm.get('customfieldNumeric')};
	 get customfieldLengthMin(){ return this.customfieldForm.get('customfieldLengthMin')};
	 get customfieldLengthMax(){ return this.customfieldForm.get('customfieldLengthMax')};
	 get customfieldUnique(){ return this.customfieldForm.get('customfieldUnique')};
	 get customfieldMandatory(){ return this.customfieldForm.get('customfieldMandatory')};
	 get customfieldInstruction(){ return this.customfieldForm.get('customfieldInstruction')};
	 get customfieldPrint(){ return this.customfieldForm.get('customfieldPrint')};
	 get customfieldActive(){ return this.customfieldForm.get('customfieldActive')};
}
	
	
export class customfieldData
{
	customfieldId: string;
	customfieldSubmoduleId: string;
	customfieldTitle: string;
	customfieldCftypeId: string;
	customfieldNumeric: string;
	customfieldLengthMin: string;
	customfieldLengthMax: string;
	customfieldOption: string;
	customfieldUnique: string;
	customfieldMandatory: string;
	customfieldInstruction: string;
	customfieldPrint: string;
	customfieldActive: string;
	customfieldRank: string;
	customfieldEntryId: string;
	customfieldEntryBy: string;
	customfieldEntryDate: string;
	customfieldModifiedId: string;
	customfieldModifiedBy: string;
	customfieldModifiedDate: string;
}


